import { Injectable } from '@angular/core';
//import { rootCertificates } from 'tls';
import { KeyValuePairMultipleFile } from './KeyValue';

@Injectable({
  providedIn: 'root',
})
export class KeyValueToJason{
  getJsonFromKeyValuePairs(keyValues: KeyValuePairMultipleFile[]) {
    const emptyObject = {};
    if (keyValues.length > 0) {
      for(let x in keyValues[0].values){
        emptyObject[x]={};
      }
      keyValues.forEach(element => {
        for (let x in element.values) {
          if(element.values[x]!=""){
          this.createJSON(emptyObject[x],element.key, element.values[x]);
          }
        }
      })
    }
    return emptyObject;
  }

  createJSON(json, keys: string, value: string) {
    const keysArray = keys.split('.');
    const last_index = keysArray.length - 1;
    keysArray.forEach((key: string, index: number) => {
      if (index == last_index) {
        json[key] = value;
      }
      else {
        if (!(key in json)) {
          json[key] = {};
        }
        json = json[key];
      }
    });
  }
}
