import { HttpClient } from '@angular/common/http';
import { Injectable, EventEmitter } from '@angular/core';

import { KeyValuePair, KeyValuePairFromMultipleFile, SelectedKeyValuePair } from './KeyValue';
import { element } from 'protractor';

@Injectable({
  providedIn: 'root'
})
export class KeyValueService {

  private inputFiles:object;
  private selectedKeyValuePair: SelectedKeyValuePair[];

  constructor(private http: HttpClient) { }

  getSelectedKeyValue(){
    return this.selectedKeyValuePair;
  }

  setSelectedKeyValue(selected){
    this.selectedKeyValuePair = selected;
  }

  setInputFiles(inputFiles){
    this.inputFiles = inputFiles;
  }

  getInputFiles(){
    return this.inputFiles;
  }

  getObjects(json: any) {
    const k: Array<string> = [];
    const keyValues: KeyValuePair[] = [];
    this.getKeyValue(json, k, keyValues);
    return keyValues;
  }

  private getKeyValue(json, keys: string[], keyValues: KeyValuePair[]) {
    for (let i in json) {
      if (json[i] instanceof Object) {
        keys.push(i);
        this.getKeyValue(json[i], keys, keyValues);
        keys.pop();
      } else {
        keys.push(i);
        const key = keys.toString().split(',').join('.');
        const value = json[i];
        const keyValue: KeyValuePair = { key, value };
        keyValues.push(keyValue);
        keys.pop();
      }
    }
  }

  getMultipleObjects(keyValues: KeyValuePairFromMultipleFile[], json: any, fileName: string) {
    const keys: Array<string> = [];
    this.getMultipleKeyValue(fileName,json, keys, keyValues);
   // console.log(keyValues.length);
    return keyValues;
  }

  getMultipleKeyValue(fileName: string, json, keys: string[], keyValues: KeyValuePairFromMultipleFile[]){
    for(let i in json){
      if(json[i] instanceof Object ){
        keys.push(i);
        this.getMultipleKeyValue(fileName,json[i],keys, keyValues);
        keys.pop();
      }else {
        keys.push(i);
        const key = keys.toString().split(',').join('.');
        const keyExists = keyValues.find(element => element.key === key)

        if(!keyExists){
          let values = new Map<string, string>();
          values.set(fileName, json[i]);

          this.updateExistingValues(keyValues, fileName, values);
          const keyValue: KeyValuePairFromMultipleFile = {key, values};
          keyValues.push(keyValue);
          keys.pop();
        }else {
          keyValues.map(element =>{ if(element.key === key ){
                            element.values.set(fileName, json[i]);
                          }
           });
           keys.pop();
        }

      }
    }
  }
  updateExistingValues(keyValues: KeyValuePairFromMultipleFile[], fileName: string, values: object){
    if(keyValues.length !== 0){
      let existingKeyOfValues = Object.keys(keyValues[0].values);
      for(let i = 0; i<existingKeyOfValues.length;i++){
        if(existingKeyOfValues[i] !== fileName){
           values[existingKeyOfValues[i]] ='';
          }
          keyValues.map(element => {
            if(element.values[fileName]=== undefined){
              element.values[fileName]= '';
            }
          })
      }

    }
  }
}


