import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { KeyValComponent } from './key-val/key-val.component';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { DownloadJsonService } from './downloadjson.service';
import { FileInputComponent } from './file-input/file-input.component';
import { SelectedKeyValComponent } from './selected-key-val/selected-key-val.component';

@NgModule({
  declarations: [
    AppComponent,
    KeyValComponent,
    FileInputComponent,
    SelectedKeyValComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
    FormsModule,
    ReactiveFormsModule
  ],
  providers: [DownloadJsonService],
  bootstrap: [AppComponent]
})
export class AppModule { }
