import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FileInputComponent } from './file-input/file-input.component';
import { KeyValComponent } from './key-val/key-val.component';
import { SelectedKeyValComponent } from './selected-key-val/selected-key-val.component';
import { AppStartGuard } from './app-start.guard';


const routes: Routes = [
  { path: 'file-input', component: FileInputComponent },
  { path: 'key-val', component: KeyValComponent, canActivate: [AppStartGuard] },
  { path: 'selected-key-val', component: SelectedKeyValComponent, canActivate: [AppStartGuard] },
  { path: '', redirectTo: '/file-input', pathMatch: 'full' },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
