import { Component, OnInit } from '@angular/core';

import { KeyValueService } from '../key-value.service';
import { KeyValuePair, KeyValuePairFromMultipleFile,SelectedKeyValuePair } from '../KeyValue';
import { DownloadJsonService } from '../downloadjson.service';
import { FormControl } from '@angular/forms';
import { BehaviorSubject, combineLatest, Observable } from 'rxjs';

import { take, map, tap, startWith } from 'rxjs/operators';
import { summaryFileName } from '@angular/compiler/src/aot/util';
import { element } from 'protractor';
import { ActivatedRoute, Router } from '@angular/router';


@Component({
  selector: 'app-key-val',
  templateUrl: './key-val.component.html',
  styleUrls: ['./key-val.component.css'],
  providers: [DownloadJsonService],
})
export class KeyValComponent implements OnInit {

  keyValues$ = new BehaviorSubject<SelectedKeyValuePair[]>([]);
  keyVal: KeyValuePairFromMultipleFile[] = [];
  uploadedFile: any;
  fileUrl: any;
  downloadDisabled: boolean = true;
  query = new FormControl('');

  filtered$: Observable<SelectedKeyValuePair[]>;

  disableNextBtn$ = this.keyValues$.pipe(
    tap(),
    map(data => {
      if (!data) {
        return true;
      }

      if (data.some(x => x.selected)) {
        return false;
      }

      return true;
    })
  );

  constructor(
    private keyValueService: KeyValueService,
    private router: Router
  ) {
    this.filtered$ = combineLatest(this.query.valueChanges.pipe(startWith('')), this.keyValues$).pipe(
      map(([query, values]) => {

        if (!query) {
          return values;
        }

        if (!values) {
          return [];
        }

        const regEx = new RegExp(query, 'i');
        return values.filter(x => x.key.match(regEx) || Object.values(x.values).filter(x => x.match(regEx)).length > 0 ? true : false);
      })
    );
    this.fileChanged(this.keyValueService.getInputFiles());
  }

  ngOnInit() {

  }

  onNext(){
    const selected = this.keyValues$.getValue().filter(x => x.selected);
    this.keyValueService.setSelectedKeyValue(selected);
    this.router.navigate(['selected-key-val']);
  }

  onBack(){
    this.keyValueService.setInputFiles({});
    this.router.navigate(['file-input']);
  }

  onCheck(keyValuePair: SelectedKeyValuePair) {

    const data = this.keyValues$.getValue();

    const updated = data.map(row => {
      if (row.key === keyValuePair.key) {
        return { ...row, selected: !row.selected };
      }
      return row;
    });
    this.keyValues$.next(updated);
  }

  async fileChanged(inputFiles){
    this.uploadedFile = inputFiles;
    this.keyVal = [];
    for(let file of this.uploadedFile){
      const fileName = file.name.replace('.json','');
      try{
        const fileContent = await this.readUploadedFile(file);
        const data = this.keyValueService.getMultipleObjects(this.keyVal, JSON.parse(fileContent as string), fileName).map(x => (<SelectedKeyValuePair>{ ...x, selected: false }));
        this.keyValues$.next(data);
      } catch(e) {
        console.log(e.message);
      }
    }
  }

  readUploadedFile(file){
    const fileReader = new FileReader();

    return new Promise((resolve, reject)=>{
      fileReader.onload = ()=>{
        resolve(fileReader.result);
      };
      fileReader.onerror =() =>{
        reject("Problem Parsing Input files");
      }
      fileReader.readAsText(file);
    });
  }

}
