import { Component, OnInit, Input } from '@angular/core';
import { KeyValueService } from '../key-value.service';
import { SelectedKeyValuePair, KeyValuePairFromMultipleFile } from '../KeyValue';
import { throwError } from 'rxjs';

import { DownloadJsonService } from '../downloadjson.service';
import { Router } from '@angular/router';
import { KeyValueToJason } from 'projects/LocalizationExcel/src/app/tojson.service';

@Component({
  selector: 'app-selected-key-val',
  templateUrl: './selected-key-val.component.html',
  styleUrls: ['./selected-key-val.component.css'],
  providers:[KeyValueToJason]
})
export class SelectedKeyValComponent implements OnInit {

  selectedKeyValuePair: SelectedKeyValuePair[] = this.keyValueService.getSelectedKeyValue();
  updatedKeyValuePair: SelectedKeyValuePair[];
  fileUrl: any;

  constructor(
    private keyValueToJason:KeyValueToJason,
    private downloadJson: DownloadJsonService,
    private keyValueService: KeyValueService,
    private router: Router ){
    this.updatedKeyValuePair = this.selectedKeyValuePair;
  }

  ngOnInit() {

  }

  onBack(){
    this.keyValueService.setSelectedKeyValue([])  ;
    this.router.navigate(['key-val']);
  }

  saveSelectedItems() {
    this.selectedKeyValuePair = this.updatedKeyValuePair;
    let modifiedJsonToParse: {key:string, values:Object}[] = this.updatedKeyValuePair.map(row => {
      let values: object = {};
      for(let [key, value] of row.values){
        values[key] = value;
      }
      return{
        key: row.key,
        values: values
      }
    });
    const finalJson =this.keyValueToJason.getJsonFromKeyValuePairs(modifiedJsonToParse);
    const localizedJson = finalJson;

    this.fileUrl = this.downloadJson.getURL(localizedJson);
    let link = document.createElement("a");
    link.download = "localization.json";
    link.href = this.fileUrl;
    link.click();
  }

  onUpdate(keyValuePair, keyChanged, event) {
    const data = this.updatedKeyValuePair;

    this.updatedKeyValuePair = data.map(row => {
      if (row.key === keyValuePair.key) {
        let values = new Map<string, string>();
        for(let [key, value] of row.values){
          if(key === keyChanged){
            values.set(keyChanged, event);
          }else{
            values.set(key, value);
          }
        }
        return{
          key: row.key,
          values: values
        };
      }

      return row;
    });

  }

}

